/**       */

import styled from "styled-components";
import { FiSearch } from "react-icons/fi";

//     
const Box = styled.div`
  position: relative;
  text-align: center;
  padding: 10px;

  .icon {
    position: absolute;
    left: 50px;
    top: 19px;
    color: #7a7a7a;
  }
`;

//  
const SearchInput = styled.input`
  width: 80%;
  padding: 8px 10px;
  text-indent: 1.8em;
  font-size: 15px;
  border: none;
  border-radius: 20px;
  outline: none;
`;

export function SearchBar(props) {
  //   StickerBox  
  const checkEnter = (e) => {
    if (e.key === "Enter") {
      props.sendWord(e.target.value);
    }
  };

  return (
    <Box>
      <FiSearch className="icon" />
      <SearchInput
        type="text"
        onKeyPress={checkEnter}
        placeholder=" ."
      ></SearchInput>
    </Box>
  );
}
