/**   
 * -      : react-rnd  
 */

import { useContext, useState } from "react/cjs/react.development";
import styled from "styled-components";
import { StiContext } from "../contexts";
import { ADD_Sticker } from "../contexts/actionTypes";
import { Rnd } from "react-rnd";
// import { TiDelete } from "react-icons/ti";

//    
const ListImg = styled.img`
  width: 75px;
  margin: 5px 10px;
  cursor: pointer;
`;

//         (react-rnd)
const StyledRnd = styled(Rnd)`
  position: relative;
  z-index: 5;
  &:hover {
    border: 1px solid #a9bac5;
  }
  &:hover .delete {
    visibility: visible;
  }

  .delete {
    visibility: hidden;
    position: absolute;
    top: 5px;
    right: 5px;
    z-index: 6;
    cursor: pointer;
    color: #a9bac5;
  }
`;

//        rnd  
const Sticker = styled.img`
  width: inherit;
`;

export function StickerItem(props) {
  const {
    state: { countKey },
    dispatch,
  } = useContext(StiContext);

  //       
  const [position, setPosition] = useState({
    x: 30,
    y: 80,
    width: 100,
    height: 100,
  });

  //     (      )
  const onResize = (event, direction, ref, delta) => {
    const { width, height } = ref.style;

    setPosition((prevPosition) => ({
      ...prevPosition,
      width,
      height,
    }));
  };

  //     (      )
  const onDragStop = (e, d) => {
    const { x, y } = d;
    setPosition((prevPosition) => ({
      ...prevPosition,
      x: x + 10,
      y: y + 10,
    }));
  };

  //      
  const addSticker = () => {
    const newStickerTag = (
      <StyledRnd
        default={position}
        onResize={onResize}
        onDragStop={onDragStop}
        bounds="parent"
        lockAspectRatio={true}
        key={countKey}
      >
        <Sticker src={props.img}></Sticker>
        {/* <TiDelete className="delete" size="25" onClick={deleteSticker} /> */}
      </StyledRnd>
    );
    dispatch({
      type: ADD_Sticker,
      payload: newStickerTag,
    });
  };

  // const deleteSticker = () => {
  //   console.log();
  // };

  return (
    <li>
      <ListImg src={props.img} onClick={addSticker}></ListImg>
    </li>
  );
}
