/**     */

import { useState } from "react";
import styled from "styled-components";
import ReactDatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

//   
const Box = styled.div`
  width: 400px;
  height: 270px;
  display: inline-block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
  background-color: #e7ecef;
  border-radius: 5px;
  z-index: 10;

  .datePicker {
    padding: 8px 10px;
    font-size: 15px;
    border: none;
    border-radius: 15px;
    outline: none;
    text-align: center;
  }
`;

//     
const BackLayer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 9;
`;

//    
const Header = styled.div`
  font-size: 20px;
  font-weight: 600;
  margin: 10px 0 20px 0;
`;

//  
const Label = styled.div`
  margin: 15px 0 10px 5px;
`;

//   
const TitleInput = styled.input`
  width: 380px;
  padding: 8px 10px;
  font-size: 15px;
  border: none;
  border-radius: 15px;
  outline: none;
`;

// DatePicker 
const AlignDate = styled.div`
  display: flex;
  align-items: center;

  .icon {
    margin-right: 10px;
  }
`;

//  
const AlignBtn = styled.div`
  text-align: center;
  margin-top: 20px;
`;

//  
const StyledButton = styled.button`
  padding: 8px 10px;
  margin: 0 5px;
  color: #ffffff;
  background-color: #2c3e50;
  border: none;
  border-radius: 4px;
  cursor: pointer;

  &:hover {
    background-color: #1b2632;
  }
`;

export function AddScheduleForm(props) {
  const [title, setTitle] = useState(props.event.title);
  const [startDate, setStartDate] = useState(props.event.start);
  const [endDate, setEndDate] = useState(props.event.end);

  // CalendarBox   () 
  const addSchedule = () => {
    props.addSchedule(title, startDate, endDate);
  };

  const upDateSchedule = () => {
    props.upDateSchedule(props.event.e.event.id, title, startDate, endDate);
  };

  const deleteSchedule = () => {
    props.deleteSchedule(props.event.e.event.id);
  };

  // CalendarBox    
  const closeForm = () => {
    props.closeForm();
  };

  return (
    <div>
      <BackLayer></BackLayer>
      <Box>
        <div>Schedule</div>
        <Header> </Header>
        <Label> ?</Label>
        <TitleInput
          type="text"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        />
        <Label> !</Label>
        <AlignDate>
          <ReactDatePicker
            selected={startDate}
            onChange={(date) => setStartDate(date)}
            dateFormat="yyyy.MM.dd(eee)"
            placeholderText=""
            className="datePicker"
          />
          <div className="icon"> ~ </div>
          <ReactDatePicker
            selected={endDate}
            onChange={(date) => setEndDate(date)}
            dateFormat="yyyy.MM.dd(eee)"
            placeholderText=""
            className="datePicker"
          />
        </AlignDate>
        <AlignBtn>
          {props.event.title ? (
            <>
              <StyledButton onClick={upDateSchedule}></StyledButton>
              <StyledButton onClick={deleteSchedule}></StyledButton>
            </>
          ) : (
            <StyledButton onClick={addSchedule}></StyledButton>
          )}
          <StyledButton onClick={closeForm}></StyledButton>
        </AlignBtn>
      </Box>
    </div>
  );
}
